import { useEffect, useRef, useState } from 'react'
import {useDispatch, useSelector} from "react-redux"
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { useForm, Controller } from 'react-hook-form'
import classnames from 'classnames'
import { DragDrop } from '@uppy/react'
import Uppy from '@uppy/core'
import Breadcrumbs from '@components/breadcrumbs'
import '@uppy/core/dist/style.css'
import thumbnailGenerator from '@uppy/thumbnail-generator'
import "../styles1.scss"
import "../styles.scss"
import * as XLSX from 'xlsx'
import '@styles/react/libs/file-uploader/file-uploader.scss'
import { Card, CardHeader, CardTitle, CardBody, Row, Col, Label, Button} from 'reactstrap'
import Preview from './preview'
import { getFootballpredictor, addFootpredictQuestion, addfootquepreview  } from '../../store/actions' 


const SelectReact = () => {
 const predictorlistRef = useRef()
  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit, control } = useForm({ defaultValues })

    const store1 = useSelector((state) => state.predictor)
    console.log(store1)
    // const [pre, setPre] = useState(0)
    const [val, setVal] = useState(false)
    
    const [error, setError] = useState(false)
    const [data, setData] = useState(null)
   // const [refresh, setRefresh] = useState(false)
    // const [img, setImg] = useState(null)
    const [imageError, setImageError] = useState(false)
    const [predilist, setpredilist] = useState("")
    const [check, setCheck]  = useState(false)
    const [preview, setPreview] = useState('')
    const [hide, setHide] = useState(false)
    const [validate, setValidate] = useState('')
 console.log(preview)
    const dispatch = useDispatch()

    const uppy = new Uppy({
      meta: { type: 'avatar' },
      restrictions: { maxNumberOfFiles: 0, allowedFileTypes:['.csv', '.xlsx', '.text'] },    
      autoProceed: true
    })
 
    uppy.use(thumbnailGenerator) 
    uppy.on('file-added', async(file, preview) => {
      console.log(file.data.lastModified)
     setData(file.data)
      const sd =  await file.data.arrayBuffer()

      /* parse and load first worksheet */
      const wob = XLSX.read(sd)
      //const wb = XLSX.readFile(data, {sheetRows: 5})
      const workheet = wob.Sheets[wob.SheetNames[0]]
      const jsoData = XLSX.utils.sheet_to_json(workheet)
      console.log(jsoData)
      setPreview(jsoData)
      console.log(jsoData.filter(res => setValidate(res.question_type === 'Multiple Choice' || res.question_type === 'Winner Selector' || res.question_type === 'Score Predictor')))
      console.log(preview)
     // setImg(preview)
    })
    console.log(validate)
  
    const predictor = store1.predict_football_name.map(function (pre) {
        return { value: pre.id, label: pre.caption }
      })   

      const reset = () => {
        predictorlistRef.current?.select?.clearValue('')
        setpredilist('')
        setCheck(false)
        setPreview('')
        setData(null)
      }

    const submit = (e) => {
      e.preventDefault()

      if (!data) {
        setImageError(true
          )
      } else {
        setImageError(false)
      }

      setVal(true)
      if (predilist === "") {
        setError(true)
      } else {
        // const params = {
        //   series_id: serilist,
        //   fixture_id: fixlist,
        //   contest_id: conlist,
        //   // feed_lang: currentlang.value,
        //   file: data
        // }
        const param = new FormData()
        param.append('predictor_id', predilist)
       // param.append('contest_id', conlist)
        param.append('file', data)
        console.log(param)
        dispatch(addFootpredictQuestion(param))              
      }
    }
    
    const predictorlist = (e) => {
      setVal(false)
      setError(false)
      console.log(error)
      setpredilist(e?.value)
      //setRefresh(true)
    
      //setSid(filterid[0]?.id)
      
    }

    useEffect(() => {
       dispatch(getFootballpredictor())
       
      }, [])
      
     // const [fileName, setFileName] = useState(null)
      // const handleFile = async (e) => {
      //   const file = e.target.files[0]
      //   console.log(file)
      //   //setFileName(file.name)
      // const data = await file.arrayBuffer()

      // /* parse and load first worksheet */
      // const wb = XLSX.read(data)
      // //const wb = XLSX.readFile(data, {sheetRows: 5})
      // const worksheet = wb.Sheets[wb.SheetNames[0]]
      // const jsonData = XLSX.utils.sheet_to_json(worksheet)
      //  setPreview(jsonData)
      //   console.log(jsonData)
      // }
     
    const functionChange = () => {
      dispatch(addfootquepreview(preview))
      setCheck(true)
      setHide(true)
    }
    console.log(hide)
  return (
    <>
    <Breadcrumbs breadCrumbTitle='Predictor' breadCrumbActive='Add' />
    <Card>
      <CardHeader>
        <CardTitle tag='h4'>{store1.isloading }</CardTitle> 
      </CardHeader>
      <CardBody>
        <p>
          <a className='my-50' target='_blank' href='https://react-select.com/home' rel='noopener noreferrer'>
           
          </a>
        </p>
        <Row>
          <Col className='mb-1' md='4' sm='12'>
            <Label>Predictor</Label>
              <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                  {...field}
                  onChange={(e) => predictorlist(e)}
                  ref = {predictorlistRef}
                  options={predictor}
                  theme={selectThemeColors}
                  className={classnames('react-select', { 'is-invalid':  predilist === "" && val })}
                  classNamePrefix='select'
                  isClearable = {true}
                  />
                  )}
                  name='reactSelect'
            />
          
          </Col>
          
        </Row>
       
  <div className="mt-2">
       {check === true ? <Preview /> : '' }
       </div>
       
        <Row className="wrapper-add" >
        <Col className="add-post-edit">
         <div className={`mt-4 ${imageError ? 'img-error-post' : ''}`}>
         <DragDrop uppy={uppy}
           locale={{
            strings: {
              dropHereOr: 'Drop file or %{browse}',
              browse: 'browse'
            }
          }}
         />
        {/* {img !== null ? <img className='rounded mt-2' src={img} alt='avatar' /> : null} */}
                {/* {img && (
                  <div className="uploaded-files">
                    <img src={img} className="post-preview" />
                  </div>
                )} */}
              </div>
              </Col>
             </Row> 
      
             <div className="mt-3">
             {preview !== '' ?  <Button.Ripple color='primary' onClick={functionChange} >Preview</Button.Ripple> : ''} &nbsp;&nbsp;
        <Button.Ripple 
        color='primary'
        disabled={validate === false}
        onClick={(e) => handleSubmit(submit(e))}
        >Submit
        </Button.Ripple>&nbsp;&nbsp;
        <Button.Ripple 
        color='primary'
        onClick={() => reset()}
        >Reset
        </Button.Ripple>
        </div>                  
      </CardBody>
    </Card>
    </>
  )
}
export default SelectReact